import React, {useState} from 'react';
import {BlipSelect} from '../BlipSelect/index'

import Answer from '../BlipAnswerResource/answerResouce';
import  MenuResource  from '../BlipMenuResource/menuResource';
import  ConfigResource  from '../BlipConfigResource/configResource';
function Resource({info}){

  const [config, setConfig] = useState();
  const [key, setKey] = useState(info || '');

  const data = [
    { value: 'resposta', label: 'Resposta' },
    { value: 'config', label: 'Config' },
    { value: 'menu', label: 'Menu' },
  ];

  return(
    
    <>
    <div className="container w-100">
      <div className="row">
        <div className="col-6">
          <label>bot key: </label>
          <input
                  name="key"
                  className="bp-input-wrapper blip-input w-100"
                  placeholder="Digite Key do bot"
                  value={info}
                 
                  required disabled={true}
                />
          </div>
          <div className="col-6">
          <label>Select a Resource: </label> 
            <BlipSelect options={data}   onSelectOption={($event) => setConfig($event['$event'].optionProps.value)} placeholder="Select"/>
          </div>
          <div className="w-100">
              {
              config === 'resposta' ? <Answer chave={info}/> : config === 'config' ? <ConfigResource chave={info}/> : config === 'menu' ? <MenuResource chave={info}/> :''
              }
          </div> 
        </div>
      </div>
    </>
      
  )

}


export default Resource;