import React, { useEffect, useState } from "react";
import "blip-toolkit/dist/blip-toolkit.css";
import { getApplication } from "./api/applicationService";
import { showToast, withLoading } from "./api/commomService";
import { PageHeader } from "components/PageHeader";
import { CommonProvider } from "contexts/CommonContext";
import { PageTemplate } from "components/PageTemplate";
import { BlipTabs } from 'blip-toolkit'

import CloneBots from 'components/CloneBots/clone';
import MarketPlace from 'components/MarketPlace/marketplace';
import Resource from 'components/BlipResource/resource';
import BuilderFlow from 'components/BuilderFlow/builder-flow';
import LogoCloneBots from './static/images/logo.png';
import WordCloud from 'components/BlipWordCloud/wordcloud';
import PluginDesk from 'components/BlipDeskPlugin/pluginDesk';


function App() {
    const [application, setApplication] = useState({ shortName: 'CloneBots', accessKey: '', key: '', tenantId: '' })




    useEffect(() => {
        withLoading(async () => {


            const { accessKey, shortName, tenantId } = await getApplication();
            const KEY = 'Key ' + btoa(`${shortName}:` + atob(accessKey));
            setApplication({ shortName, accessKey, key: KEY, tenantId });

            showToast({
                type: "success",
                message: "Success loaded"
            });

            new BlipTabs('tab-nav');
        });


    }, [])




    const title = `CloneBots`;
    const logo = LogoCloneBots;
    const keyTarget = application.key;
    const shortName = application.shortName;
    const tenantId = application.tenantId;
    const accessKey = application.accessKey;

    return (
        <CommonProvider>
            <div id="main" className="App">
                <PageHeader title={title} logo={logo} />
                <PageTemplate title={title}>
                    <div id="tab-nav" className="bp-tabs-container">
                        <ul className="bp-tab-nav">
                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="clonebots">Clone bots</a>
                            </li>
                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="marketplace">MarketPlace</a>
                            </li>
                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="resource">Resource</a>
                            </li>
                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="flow">Builder flow</a>
                            </li>

                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="pluginDesk">BroadCast Desk</a>
                            </li>

                            <li>
                                {/* eslint-disable-next-line */}
                                <a href="#" data-ref="wordcloud">WordCloud</a>
                            </li>
                        </ul>
                        <div className="bp-tab-content fl w-100" data-ref="clonebots">
                            <CloneBots info={keyTarget} />
                        </div>
                        <div className="bp-tab-content fl w-100" data-ref="marketplace">
                            <MarketPlace info={keyTarget} />

                        </div>
                        <div className="bp-tab-content fl w-1000" data-ref="resource">
                            <Resource info={keyTarget} />

                        </div>
                        <div className="bp-tab-content fl w-1000" data-ref="flow">
                            <BuilderFlow info={keyTarget} />

                        </div>
                        <div className="bp-tab-content fl w-1000" data-ref="wordcloud">
                            <WordCloud info={keyTarget} />

                        </div>
                        <div className="bp-tab-content fl w-1000" data-ref="pluginDesk">
                            <PluginDesk info={keyTarget} tenantId={tenantId} shortName={shortName} accessKey={accessKey} />

                        </div>
                    </div>
                </PageTemplate>
            </div>
        </CommonProvider>
    )
}

export default App