import api from '../baseUrl';

export default (guid, type, items, value) => {
  return api.post('/commands', {
    "id": guid,
    "method": "set",
    "uri":  encodeURI("/resources/"+ items.trim()),
    "type": type,
    "resource": value

  },{
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Key cGx1Z2luY29uZmlnOlpFdFFaTTdyWEx6V3JUY0J5a2ZR'
  }});
}

